import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:[{name:1,root:2}],
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
            showShare:"",
            realShow:"none",
            flag:false,
            first:"",
            second:[0]

        }

    }
    componentDidMount(){
        var newdateArr = "badiu"

        var _this = this;
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

            console.log( $.type(data))

            _this.setState({newsData:data})
        })
    }
    showImg(event){
       event.stopPropagation()
       console.log("open")
       if(event.target.className!="shareButton"){return}
       event.preventDefault()
       var index =  event.target.getAttribute("data")
       this.setState({realShow:index})



    }
    showImgReal(index){
        return index==this.state.realShow?"sharBoard-active":"sharBoard"
    }
    hideImg(event){
            console.log("closed")
            /*this.setState({flag:true})
             */
            this.setState({second:1})

            this.setState({realShow:"400"})
    }
    render(){
        return(
            <div className="newsImgparent">
                {this.state.newsData.map( (item,index) =>{
                    return(
                        <div key={index} className="newsImg">
                            <div>
                                 <img src={'public/css/img/'+item.img+".jpeg"}/>
                            </div>

                            <div className="fx1" >
                                <h2>{item.word}</h2>
                                <div onMouseOver={this.showImg.bind(this)}
                                     data = {index} className="shareButton">

                                    <div className={this.showImgReal(index)} >
                                        <ul class="shareBtn16" id="shares" >
                                            <li className="jiantou"></li>
                                            <li className="shareButton" id="share2qzone">
                                                <a href="javascript:void(0)" className="s_qzone" >
                                                    QQ空间
                                                </a>
                                            </li>
                                            <li className="shareButton" id="share2qq">
                                                <a href="javascript:void(0)" className="s_qq" onclick="share2qq.showPopup(this,'少林寺首届“武林大会” 众洋弟子参赛','http://henan.qq.com/a/20170729/017564.htm','http://inews.gtimg.com/newsapp_ls/0/1855649162_640330/0')">
                                                    QQ好友
                                                </a>
                                            </li>
                                            <li id="share2sina" className="shareButton">
                                                <a href="javascript:void(0)" className="s_sina" onclick="share2sina('少林寺首届“武林大会” 众洋弟子参赛','http://henan.qq.com/a/20170729/017564.htm','http://inews.gtimg.com/newsapp_ls/0/1855649162_640330/0')">
                                                    新浪微博
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>

                             </div>

                        </div>
                        )

                })}
            </div>
        )
    }
}


